import React, { useEffect, useState } from 'react';
import { Grid, TextField, Typography, Box, Select, MenuItem, InputBase, TextareaAutosize } from '@mui/material';
import LoadingButton from '@mui/lab/LoadingButton';
import FormControlWrapper from 'src/components/FormControlWrapper';
import { styled, css } from '@mui/material/styles';
import { Formik } from 'formik';
import * as Yup from 'yup';
import FormLabel from 'src/components/FormLabel';
import Page from 'src/components/Page';
import API from 'src/utils/api';
import Alert from 'src/components/Alert'

const ContactBox = styled(Box)(
  () => css`
    width: 100%;
    max-width: 1024px;
    background: white;
    margin: 3rem 0;
    padding: 4rem 4rem;
    border-radius: 0.5rem;
  `
);
const style = {
  fontWeightBold: {
    fontWeight: 'bold'
  },
  textarea: {
    marginTop: '6px',
    borderRadius: 4,
    position: 'relative',
    backgroundColor: '#fff',
    border: '1px solid #ced4da',
    fontSize: 16,
    padding: '8px 26px 8px 12px',
    // Use the system font instead of the default Roboto font.
    fontFamily: [
      '-apple-system',
      'BlinkMacSystemFont',
      '"Segoe UI"',
      'Roboto',
      '"Helvetica Neue"',
      'Arial',
      'sans-serif',
      '"Apple Color Emoji"',
      '"Segoe UI Emoji"',
      '"Segoe UI Symbol"'
    ].join(','),
    '&:focus': {
      borderRadius: 4,
      borderColor: '#80bdff',
      boxShadow: '0 0 0 0.2rem rgba(0,123,255,.25)'
    },
    width: '100%',
    resize: 'unset'
  },
};

const BootstrapInput = styled(InputBase)(({ theme }) => ({
  'label + &': {
    marginTop: theme.spacing(3)
  },
  '& .MuiInputBase-input': {
    marginTop: '6px',
    borderRadius: 4,
    position: 'relative',
    backgroundColor: theme.palette.background.paper,
    border: '1px solid #ced4da',
    fontSize: 16,
    padding: '8px 26px 8px 12px',
    transition: theme.transitions.create(['border-color', 'box-shadow']),
    // Use the system font instead of the default Roboto font.
    fontFamily: [
      '-apple-system',
      'BlinkMacSystemFont',
      '"Segoe UI"',
      'Roboto',
      '"Helvetica Neue"',
      'Arial',
      'sans-serif',
      '"Apple Color Emoji"',
      '"Segoe UI Emoji"',
      '"Segoe UI Symbol"'
    ].join(','),
    '&:focus': {
      borderRadius: 4,
      borderColor: '#80bdff',
      boxShadow: '0 0 0 0.2rem rgba(0,123,255,.25)'
    }
  }
}));

const LoginView = () => {
  const [subject, setSubject] = useState('');
  const [subjects, setSubjects] = useState('');
  const [open, setOpen] = useState(false);
  const [alertError, setAlertError] = useState(false);
  const [alertMessage, setAlertMessage] = useState('');

  useEffect(() => {
    try {
      async function getSubject() {
        const response = await API.get('support?support_type=1');
        const newVal = response.data.data.map(x=>{
          return {
            ...x,
            value: x.content.value,
            lang_code: x.content.lang_code,
          }
        })
        setSubjects(newVal);
      }
      getSubject();
    } catch (err) {
      console.log('Load profile KYC error', err)
    }
  }, []);

  return (
    <Page title="Register New Account">
      <Grid container sx={{ height: '100vh' }}>
        <Grid item xs={12} sx={{ background: '#E5E5E5', display: 'flex', flexDirection: 'column', justifyContent: 'center', alignItems: 'center' }}>
          <ContactBox>
            <Formik
              enableReinitialize 
              initialValues={{
                first_name: '',
                last_name: '',
                email: '',
                message: '',
                // content: ''
              }}
              validationSchema={Yup.object().shape({
                first_name: Yup.string().required('First name is required'),
                last_name: Yup.string().required('Last is required'),
                email: Yup.string().required('Email is required'),
                message: Yup.string().required('Message is required'),
                // title: Yup.string().required('Subject is required'),
              })}
              onSubmit={async (values, { setSubmitting, setFieldValue }) => {
                try {
                  const params = {
                    content: values.message,
                    email: values.email,
                    first_name: values.first_name,
                    lang_code: subject.lang_code,
                    last_name: values.last_name,
                    support_id: subject.id
                  }
                  const response = await API.post('support-ticket', params, {headers: {
                    "Content-Type": "application/json",
                    "Accept": "application/json"
                  }});
                  if (response && response.status === 200) {
                    setFieldValue('first_name', '')
                    setFieldValue('last_name', '')
                    setFieldValue('email', '')
                    setFieldValue('message', '')
                    setSubject('')
                    setSubmitting(false)
                    setOpen(true)
                  }
                } catch (error) {
                  setAlertError(true)
                  setAlertMessage('Something wrong, please try again later!')
                  setOpen(true)
                }
              }}
            >
              {({ errors, handleBlur, handleChange, handleSubmit, isSubmitting, touched, values }) => (
                <form onSubmit={handleSubmit}>
                  <Typography variant="h5" component="div" style={style.fontWeightBold} align="center" marginBottom="32px">
                    Contact form
                  </Typography>
                  
                  <Box sx={{ display: 'flex' }}>
                    <FormControlWrapper marginRight={2}>
                      <FormLabel label="First name" required />
                      <TextField
                        fullWidth
                        size="small"
                        margin="dense"
                        name="first_name"
                        placeholder="First name"
                        error={Boolean(touched.first_name && errors.first_name)}
                        helperText={touched.first_name && errors.first_name}
                        onBlur={handleBlur}
                        onChange={handleChange}
                        value={values.first_name}
                      />
                    </FormControlWrapper>
                    <FormControlWrapper marginLeft={2}>
                        <FormLabel label="Last name" required />
                        <TextField
                          fullWidth
                          size="small"
                          margin="dense"
                          name="last_name"
                          placeholder="Last name"
                          error={Boolean(touched.last_name && errors.last_name)}
                          helperText={touched.last_name && errors.last_name}
                          onBlur={handleBlur}
                          onChange={handleChange}
                          value={values.last_name}
                        />
                      </FormControlWrapper>
                  </Box>

                  <Box sx={{ display: 'flex' }}>
                    <FormControlWrapper>
                      <FormLabel label="Email" required />
                      <TextField
                        fullWidth
                        size="small"
                        margin="dense"
                        name="email"
                        placeholder="Email"
                        error={Boolean(touched.email && errors.email)}
                        helperText={touched.email && errors.email}
                        onBlur={handleBlur}
                        onChange={handleChange}
                        value={values.email}
                      />
                    </FormControlWrapper>
                  </Box>

                  <Box sx={{ display: 'flex' }}>
                    <FormControlWrapper>
                      <FormLabel label="Subject" required />
                      {/* <TextField
                          fullWidth
                          size="small"
                          margin="dense"
                          name="content"
                          placeholder="Subject"
                          error={Boolean(touched.content && errors.content)}
                          helperText={touched.content && errors.content}
                          onBlur={handleBlur}
                          onChange={handleChange}
                          value={values.content}
                        /> */}
                      {
                        subjects ? <Select
                        value={subject}
                        onChange={(event) => {
                          setSubject(event.target.value);
                        }}
                        fullWidth
                        input={<BootstrapInput />}
                      >
                        {subjects
                          ? subjects.map((x) => {
                              return <MenuItem value={x} key={x.id}>{x.value}</MenuItem>;
                            })
                          : ''}
                      </Select> : ''
                      }
                    </FormControlWrapper>
                  </Box>
                  <Box sx={{ display: 'flex' }}>
                    <FormControlWrapper>
                      <FormLabel label="Message" required />
                      <TextareaAutosize
                        minRows={5}
                        style={style.textarea}
                        placeholder="Message"
                        name="message"
                        error={Boolean(touched.message && errors.message)}
                        helperText={touched.message && errors.message}
                        onBlur={handleBlur}
                        onChange={handleChange}
                        value={values.message}
                      />
                    </FormControlWrapper>
                  </Box>

                  <Box mt={4} textAlign="center">
                    <LoadingButton variant="contained" sx={{ width: '50%' }} type="submit" loading={isSubmitting}>
                      SUBMIT
                    </LoadingButton>
                  </Box>
                </form>
              )}
            </Formik>
          </ContactBox>
        </Grid>
      </Grid>

      <Alert open={open} setOpen={ () => {setOpen(false)}} message={alertMessage} error={alertError} />
    </Page>
  );
};

export default LoginView;
